<script>
// @ is an alias to /src
import Search from '@/components/Search.vue';
import NavLeft from '@/components/NavLeft/NavLeft.vue';
import NavCenter from '@/components/NavCenter.vue';
import ImgShowTop from '@/components/pictureShow/ImgShowTop.vue';
import ImgShowBottom from '@/components/pictureShow/ImgShowBottom.vue';
import LoginInfo from '@/components/NavRight/LoginInfo.vue';
import NavRight01 from '@/components/NavRight/NavRight01.vue';
import NavRight02 from '@/components/NavRight/NavRight02.vue';
import NavRight03 from '@/components/NavRight/NavRight03.vue';
import GoodStuff from '@/components/goodStuff/GoodStuff.vue';
import EveryDayShop from '@/components/everyDayShop/EveryDayShop.vue';
import Fashion from '@/components/fashion/Fashion.vue';
import Live from '@/components/live/Live.vue';
import Major from '@/components/major/Major.vue';
import HotShHop from '@/components/hotShop/HotShop.vue';
import YouLove from '@/components/youLove/YouLove.vue';
import End from '@/components/End.vue';
import Redactor from '@/components/Redactor.vue';
import SearchTop from '@/components/SearchTop.vue';
import HtmlNav from '@/components/HtmlNav.vue';

export default {
  name: 'home',
  data(){
    return{
      searchTopShow:false,
      NavNum:1,
      NavNumShow:false,
      NumShow:1
    }
  },
  components: {
    Search,
    NavLeft,
    NavCenter,
    ImgShowTop,
    ImgShowBottom,
    LoginInfo,
    NavRight01,
    NavRight02,
    NavRight03,
    GoodStuff,
    EveryDayShop,
    Fashion,
    Live,
    Major,
    HotShHop,
    YouLove,
    End,
    Redactor,
    SearchTop,
    HtmlNav
  },
  mounted() {
    window.addEventListener("scroll", this.showIcon);
  },
  methods: {
    showIcon() {
        if (!!document.documentElement.scrollTop && document.documentElement.scrollTop > 100) {            
             this.searchTopShow = true;
        } else {
             this.searchTopShow = false;
        }
        if (!!document.documentElement.scrollTop && document.documentElement.scrollTop <= 345) {            
             this.NavNumShow = false;

        }
        if (!!document.documentElement.scrollTop && document.documentElement.scrollTop > 345) {            
             this.NavNumShow = true;
        }
        if (!!document.documentElement.scrollTop && document.documentElement.scrollTop > 845) {            
             this.NumShow = 6;
        }
        if (!!document.documentElement.scrollTop && document.documentElement.scrollTop <= 845) {            
             this.NumShow = 1;
        }
        if (!!document.documentElement.scrollTop && document.documentElement.scrollTop <= 1350) {            
             this.NavNum = 1;
        }
        if (!!document.documentElement.scrollTop && document.documentElement.scrollTop > 1350) {            
             this.NavNum = 2;
        }
        if (!!document.documentElement.scrollTop && document.documentElement.scrollTop > 2000) {            
             this.NavNum = 3;
        }
        if (!!document.documentElement.scrollTop && document.documentElement.scrollTop > 3200) {            
             this.NavNum = 4;
        }
        if (!!document.documentElement.scrollTop && document.documentElement.scrollTop > 4100) {            
             this.NavNum = 5;
        }
      },
      htmlNavChildWay(data){
        // console.log(data);
        if(data == 1){
           document.documentElement.scrollTop = document.body.scrollTop = 0;
           this.NavNumShow = false;
           this.NavNum = 1;
        } 
        if(data == 2){
           document.documentElement.scrollTop = document.body.scrollTop = 1650;
           this.NavNum = 2;
        } 
        if(data == 3){
           document.documentElement.scrollTop = document.body.scrollTop = 2200;
           this.NavNum = 3;
        } 
        if(data == 4){
           document.documentElement.scrollTop = document.body.scrollTop = 3300;
           this.NavNum = 4;
        } 
        if(data == 5){
           document.documentElement.scrollTop = document.body.scrollTop = 4200;
           this.NavNum = 5;
        } 
        if(data == 6){
           document.documentElement.scrollTop = document.body.scrollTop = 0;
           this.NavNumShow = false;
           this.NavNum = 1;
        }
      }
  }
}
</script>

