<template>
    <div class="navRight02Child01">
        <div class="navRight02Child01-x" @click="closeShow()">×</div>
        <div class="navRight02Child01-01">
            <ul>
                <li :class="{'colorThe': showNum == index}" v-for="(item,index) in the" :key='item.id'  @mouseover="show03(index)">
                    {{item}}
                </li>
            </ul>
        </div>
            <transition :name="styName" mode="out-in">
            <div v-show="showNum == 0">
                <NavRight02Child0301/>
            </div>
            </transition>
            <transition :name="styName" mode="out-in">
            <div v-show="showNum == 1">              
                <NavRight02Child0302/>    
            </div>
            </transition>

            <transition :name="styName" mode="out-in">
            <div v-show="showNum == 2">
                 <NavRight02Child0303/>
            </div>
            </transition>

            <transition :name="styName" mode="out-in">
            <div v-show="showNum == 3">   
                 <NavRight02Child0304/>
            </div>
            </transition>

            <transition :name="styName" mode="out-in">
            <div v-show="showNum == 4">   
                 <NavRight02Child0305/>
            </div>
            </transition>

    </div>
</template>

<script>
import NavRight02Child0301 from '@/components/NavRight/NavRight02Child0301.vue';
import NavRight02Child0302 from '@/components/NavRight/NavRight02Child0302.vue';
import NavRight02Child0303 from '@/components/NavRight/NavRight02Child0303.vue';
import NavRight02Child0304 from '@/components/NavRight/NavRight02Child0304.vue';
import NavRight02Child0305 from '@/components/NavRight/NavRight02Child0305.vue';
export default {
    name:'navRight02Child01',
    data(){
        return{
            the:["车险","意外险","健康险","旅行险","财产险"],
            showNum:false,
            closeShowData:false,
            styName:'turn-left'
        }
    },
    components:{
        NavRight02Child0301,
        NavRight02Child0302,
        NavRight02Child0303,
        NavRight02Child0304,
        NavRight02Child0305
    },
    methods:{
        show03:function(index){
        if(this.showNum > index){
            this.styName = 'turn-left';
        }else{
            this.styName = 'turn-right';
        }
            this.showNum = index;
        },
        closeShow:function(){
            this.$emit('child',this.closeShowData);
            // console.log(222);
        }
    }   
}
</script>

<style lang="less" scoped>
@import "./../../themes/base.less";
.navRight02Child01-x{
    position: absolute;
    right: 4px;
    top: -8px;
    cursor: pointer;
    opacity: 0.5;
    font-size: 20px;
}
.navRight02Child01{
    overflow: hidden;
    width: 100%;
    position: relative;
}
.navRight02Child01-01{
    ul{
        padding: 0;
        margin: 0;
        display: flex;
        justify-content: space-around;
        padding-left:8px; 
        padding-right: 20px;
        margin-top: 9px;
        margin-bottom: 10px;
    }
    li{
        list-style: none;
        font-size: 12px;
        cursor: pointer;
    }
}
.colorThe{
    color:@color-primary;
}

//过度
//过度

.turn-left-enter {
    transform: translate3d(100%, 0, 0);
  }
  .turn-left-leave{
    //   transform: translate3d(-100%, -100%, 0);
     opacity: 0;
  }
  .turn-left-leave-to {
    /* transform: translate3d(-20%, 0, 0); */
    // transform: translate3d(-100%, -100%, 0);
    opacity: 0;
  }
  .turn-left-enter-active{
    transition: transform .5s ease;
  }
    .turn-left-leave-active{
    // transition: transform .5s ease;
    // transform: translate3d(-100%, -100%, 0);
  }

.turn-right-enter {
    transform: translate3d(-100%, 0, 0);
  }
  .turn-right-leave-to {
    /* transform: translate3d(-20%, 0, 0); */
    opacity: 0;
  }
.turn-right-leave {
    /* transform: translate3d(-20%, 0, 0); */
    opacity: 0;
}
  .turn-right-enter-active {
    transition: transform .5s ease;
}

</style>