                    <p v-for="(itemChild,index) in item" :key="itemChild.id">
                        <span><a>{{itemChild.name}}</a></span>
                        <span class="navleft-left-right" v-if="(index+1)%3 == 0">&gt;</span>
                        <span v-else>/</span>
                    </p>
                </li>
            </ul>
        </div>
        <div class="navleft-right" v-show="navleftRightShow" @mouseover="showRight()"  @mouseout="unShowNav">
