<script>
import NavLeftChild01 from '@/components/NavLeft/NavLeftChild01'
import NavLeftChild02 from '@/components/NavLeft/NavLeftChild02'
import DB from '@/data/NavLeftData.json';
import IMG from '@/data/NavLeftData01.json';
export default {
    name:'navLeft',
    data:function(){
        return {
            info:DB,
            infoChild:[],
            img:IMG,
            infoImg:[],
            navleftRightShow:false
        }
    },
    components:{
        NavLeftChild01,
        NavLeftChild02
    },
    computed:{
       
    },
    methods:{
        showNav:function(index){
            this.infoChild = this.info[index];
            this.infoImg = this.img[index];
            this.navleftRightShow = true;
        },
        unShowNav:function(){
            this.navleftRightShow = false;
        },
        showRight:function(){
            this.navleftRightShow = true;
        }
    }
}
</script>

