<template>
    <div class="navRight02Child0104">
        <div class="navRight02Child01-02">
            <div><input type="text" :value="valueParent" :placeholder="theme"><img src="@/assets/navRight02/down.png" @click="show03()"></div>
            <div><input type="text" placeholder="请输入手机号码"><img src="@/assets/navRight02/phone.png"></div>
            <div><input type="text" :value="valueDB"><img src="@/assets/navRight02/down.png" @click="show01()"></div>
            <div v-if='showDom01'>
                <ul>
                    <li :class="{'colorLi':index == ind}" v-for="(item,index) in DB" :key="item.id"  @click="show02(index)">
                        {{item.con1}}
                    </li>
                    <li></li>
                </ul>
            </div>
        </div>
        <div class="navRight02Child01-0304">
            <div class="navRight02Child01-04"><button>立即充值</button></div>
            <div class="navRight02Child01-03"><p>售价</p><p>￥</p><p>{{DB[ind].con2}}</p></div>
        </div>
         <div class="NavRight02-Child010401" v-show="showChild" @mouseout="unShow()" @mouseover="show04()">
            <NavRight02Child010401 @child="childData" @child1="childData1" />
        </div>
    </div>
</template>

<script>
import NavRight02Child010401 from '@/components/NavRight/NavRight02Child010401.vue';
export default {
    data(){
        return{
            DB:[
                {"con1":"100元","con2":'98-99.5'},
                {"con1":"10元","con2":'9.8-9.9'},
                {"con1":"200元","con2":'196-199'},
                {"con1":"20元","con2":'19.6-20'},
                {"con1":"300元","con2":'294-299'},
                {"con1":"30元","con2":'29.4-30'},
                {"con1":"500元","con2":'490-498'},
                {"con1":"50元","con2":'49-49.8'},
                {"con1":"1000元","con2":'980-990'}
                ],
            showNum:0,
            showDom01:false,
            valueDB:"50元",
            ind:7,
            valueChild:'',
            showChild:false,
            the:"电信"
        }
    },
    components:{
        NavRight02Child010401
    },
    computed:{
        theme:function(){
            return this.the+'/城市';
        },
        valueParent:function(){
            if (this.valueChild == ''){
                return ''
            }else {
                return this.the+'/'+this.valueChild;
            }
            
        }
    },
    methods:{
        show01:function(){
            if (this.showDom01 == true) {
                this.showDom01 = false;
            }else {
                this.showDom01 = true;
            }
        },
        show02:function(index){
            this.ind = index;
            this.valueDB = this.DB[index].con1;
            this.showDom01 = false;
        },
        show03:function(){
            if (this.showChild == true) {
                this.showChild = false;
            }else {
                this.showChild = true;
            }
        },
        unShow:function(){
            this.showChild = false;
        },
        show04:function(){
            this.showChild = true;
        },
        childData(data){
            this.valueChild = data[0];
            this.showChild = data[1];
            this.the = data[2];
        },
        childData1(data){
            this.the = data;
        }
    }   
}
</script>

