<template>
    <div class="showImg" @mouseover="stopShow" @mouseout="playShow">
        <div class="logo">
            <div>
                <img src="@/assets/pictureShowBottom/tianmaologo.jpg">
                <p>理想生活上天猫</p>
            </div>
            <div>
                {{showNum/2+1}}/6
            </div>
        </div>
        <div class="navTop" v-show="navTop">
            <p><img src="@/assets/pictureShowTop/showLeft.jpg"  @click="showLeft"></p>
            <p><img src="@/assets/pictureShowTop/showRight.jpg" @click="showRight"></p>
        </div>
        <div class="navBottom">
            <ul>
                <li v-for="(item,index) in [1,2,3,4,5,6]" :key="item.id" :class="{liColor:index == showNum/2}" @click="showBottom(index*2)"></li>
            </ul>
        </div>
        <div class="pic"><img :class="{firstImg:showNum == 0}" :src="showSrc"><img v-if="showNum != 0" :src="showSrc1"></div>
    </div>
</template>

<script>
export default {
    name:'imgShowBottom',
    data:function(){
        return {
            img:[
                require('@/assets/pictureShowBottom/showImg01.jpg'),
                require('@/assets/pictureShowBottom/showImg01.jpg'),
                require('@/assets/pictureShowBottom/showImg02.jpg'),
                require('@/assets/pictureShowBottom/showImg03.jpg'),
                require('@/assets/pictureShowBottom/showImg04.jpg'),
                require('@/assets/pictureShowBottom/showImg05.jpg'),
                require('@/assets/pictureShowBottom/showImg06.jpg'),
                require('@/assets/pictureShowBottom/showImg07.jpg'),
                require('@/assets/pictureShowBottom/showImg08.jpg'),
                require('@/assets/pictureShowBottom/showImg09.jpg'),
                require('@/assets/pictureShowBottom/showImg10.jpg'),
                require('@/assets/pictureShowBottom/showImg11.jpg'),

            ],
            showNum:0,
            showSrc:'',
            showSrc1:'',
            timeout:'',
            navTop:false
        }
    },
    computed:{
        // showSrc1(){
        //     return this.img[this.showNum+1]
        // }
    },
    mounted(){
        this.showNum = 0;
        this.showSrc = this.img[0];
        this.showSrc1 = this.img[1]
        this.init();
    },
    methods:{
        init:function(){
            this.timeout = setInterval(showNum=>{
                this.showNum += 2;
                if (this.showNum == 12) {
                    this.showNum = 0;
                }
                if (this.showNum == 0){
                    this.showSrc = this.img[0]
                     this.showSrc1 = this.img[1]
                }else {
                    this.showSrc = this.img[this.showNum]
                    this.showSrc1 = this.img[this.showNum +1]
                }
            //    console.log(this.showSrc);
            //    console.log(this.showSrc1);

            },3000)
        },
        stopShow:function(){
            clearInterval(this.timeout);
            this.navTop = true;
        },
        playShow:function(){
            this.init();
             this.navTop = false;
        },
        showLeft:function(){
            clearInterval(this.timeout);
            if (this.showNum == 0) {
                 this.showSrc = this.img[10];
                 this.showSrc1 = this.img[11];
                  this.showNum = 10
            }else {
                this.showNum -= 2
                this.showSrc = this.img[this.showNum];
                this.showSrc1 = this.img[this.showNum+1];
            }
        },
        showRight:function(){
            clearInterval(this.timeout);
            if (this.showNum == 10) {
                this.showNum = 0
                 this.showSrc = this.img[0];
                 this.showSrc1 = this.img[1];
            }else {
                this.showNum += 2
                this.showSrc = this.img[this.showNum];
                this.showSrc1 = this.img[this.showNum+1];
            }
            // console.log(this.showSrc);
        },
        showBottom(index){
           clearInterval(this.timeout); 
            this.showSrc = this.img[index];  
            this.showSrc1 = this.img[index+1]; 
            this.showNum = index;
        }
    }

}
</script>

<style lang="less" scoped>
.logo{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 20px;
    div:first-child{
        padding: 0;
        img{
            // width: 124px;
            height: 24px; 
            position: relative;
            top: 6px;
        }
        display: flex;
        p{
            font-size: 12px;
            color: #666666;
        }
    }
    div:last-child{
        font-size: 13px;
    }
}
.showImg{
    cursor: pointer;
    position: relative;
    // width: fit-content;
    background-color: #f4f4f4;
    width: 520px !important;
}
.navTop{
    width: 100%;
    position: absolute;
    display: flex;
    top:98px;
    justify-content: space-between;
    img{
        width: 12px;
        position: relative;
    }
    p:first-child img{
        left: -3px;
    }
    p:first-child{
        border-top-right-radius: 15px;
        border-bottom-right-radius: 15px;
    }
    p:last-child{
        border-top-left-radius: 15px;
        border-bottom-left-radius: 15px;
        img{
            right: -3px;
        }
    }
    p{
        display: inline-block;
        height: 30px;
        width: 18px;
        margin: 0;
        line-height: 30px;
        background-color: rgba(51, 43, 43, 0.5);
        padding: 0;
       
    }
    p:hover{
         background-color: rgba(51, 43, 43, 0.8);
    }
}
.pic{
    width: 520px;
    display: flex;
    img{
        width: 256px;
        height: 189px;
    }
    img:first-child{
        margin-right: 8px;
    }
    .firstImg{
        width:520px !important;
    }
    
}
.navBottom{
    width: 100%;
    // position: absolute;
    display: flex;
    bottom:6px;
    justify-content: center;
    margin-bottom: 8px;
}
.navBottom ul{
    margin: 0;
    padding: 0;
    background-color: rgba(224, 211, 211, 0.4);
    width: 100%;
    height: 3px;
    display: flex;
    justify-content: space-around;
    box-sizing: border-box;
    border-radius: 6px;
    align-items: center;
    li{
        box-sizing: border-box;
        width: 16.66%;
        height: 3px;
        background-color:rgb(243, 41, 108);
        list-style: none;
    }
}
.liColor{
    background-color: black !important;
}


</style>