<template>
    <div class="showImg" @mouseover="stopShow" @mouseout="playShow">
        <div class="navTop" v-show="navTop">
            <p><img src="@/assets/pictureShowTop/showLeft.jpg"  @click="showLeft"></p>
            <p><img src="@/assets/pictureShowTop/showRight.jpg" @click="showRight"></p>
        </div>
        <div class="pic"><img :src="showSrc"></div>
        <div class="navBottom">
            <ul>
                <li v-for="(item,index) in [1,2,3,4,5]" :key="item.id" :class="{liColor:index == showNum}" @click="showBottom(index)"></li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name:'imgShowTop',
    data:function(){
        return {
            img:[
                require('@/assets/pictureShowTop/showImg01.jpg'),
                require('@/assets/pictureShowTop/showImg02.jpg'),
                require('@/assets/pictureShowTop/showImg03.jpg'),
                require('@/assets/pictureShowTop/showImg04.jpg'),
                require('@/assets/pictureShowTop/showImg05.jpg')
            ],
            showNum:0,
            showSrc:'',
            timeout:'',
            navTop:false
        }
    },
    mounted(){
        this.showNum = 0;
        this.showSrc = this.img[0];
        this.init();
    },
    methods:{
        init:function(){
            this.timeout = setInterval(showNum=>{
                this.showNum += 1;
                if (this.showNum == 5) {
                    this.showNum = 0;
                }
                this.showSrc = this.img[this.showNum]

            },2500)
        },
        stopShow:function(){
            clearInterval(this.timeout);
            this.navTop = true;
        },
        playShow:function(){
            this.init();
            this.navTop = false;
        },
        showLeft:function(){
            clearInterval(this.timeout);
            if (this.showNum == 0) {
                 this.showSrc = this.img[4];
                  this.showNum = 4
            }else {
                this.showNum -= 1
                this.showSrc = this.img[this.showNum];
            }
        },
        showRight:function(){
            clearInterval(this.timeout);
            if (this.showNum == 4) {
                this.showNum = 0
                 this.showSrc = this.img[0];
            }else {
                this.showNum += 1
                this.showSrc = this.img[this.showNum];
            }
            // console.log(this.showSrc);
        },
        showBottom(index){
           clearInterval(this.timeout); 
            this.showSrc = this.img[index];  
            this.showNum = index;
        }
    }

}
</script>

<style lang="less" scoped>
.showImg{
    background-color: #ffffff;
    cursor: pointer;
    position: relative;
    width: fit-content;
    width: 520px;
}
.navTop{
    width: 100%;
    position: absolute;
    display: flex;
    top:125px;
    justify-content: space-between;
    img{
        width: 12px;
        position: relative;
    }
    p:first-child img{
        left: -3px;
    }
    p:first-child{
        border-top-right-radius: 15px;
        border-bottom-right-radius: 15px;
    }
    p:last-child{
        border-top-left-radius: 15px;
        border-bottom-left-radius: 15px;
        img{
            right: -3px;
        }
    }
    p{
        display: inline-block;
        height: 30px;
        width: 18px;
        margin: 0;
        line-height: 30px;
        background-color: rgba(51, 43, 43, 0.5);
        padding: 0;
       
    }
    p:hover{
         background-color: rgba(51, 43, 43, 0.8);
    }
}
.pic{
    img{
        width: 520px;
        height: 275px;
    }
    
}
.navBottom{
    width: 100%;
    position: absolute;
    display: flex;
    bottom:6px;
    justify-content: center;
}
.navBottom ul{
    padding: 0 2px;
    background-color: rgba(224, 211, 211, 0.4);
    width: 72px;
    height: 12px;
    display: flex;
    justify-content: space-around;
    box-sizing: border-box;
    border-radius: 6px;
    align-items: center;
    li{
        width: 8px;
        height: 8px;
        background-color:white;
        list-style: none;
        border-radius: 4px;
    }
}
.liColor{
    background-color: coral !important;
}

</style>