<template>
    <div class="navRight02">
        <ul>
            <li v-for="(item,index) in img" :key="item.id" @mouseover="showWay(index)" :class="{'navRight02-li':index == infoIndex&&(index == 1 || index == 2 || index == 0 )&&show}">
                <img :src="item.img">
                <p>{{item.name}}</p>
            </li>
        </ul>
        <div class="navRight02-div" v-if="ind == 0 && closeShow">
            <NavRight02Child01 @child="childData"/>
        </div>
        <div class="navRight02-div"  v-if="ind == 1 && closeShow">
            <NavRight02Child02 @child="childData"/>
        </div>
        <div class="navRight02-div"  v-if="ind == 2 && closeShow">
            <NavRight02Child03 @child="childData"/>
        </div>
    </div>
</template>

<script>
import NavRight02Child01 from '@/components/NavRight/NavRight02Child01.vue';
import NavRight02Child02 from '@/components/NavRight/NavRight02Child02.vue';
import NavRight02Child03 from '@/components/NavRight/NavRight02Child03.vue';
export default {
    name:'navRight02',
    data:function(){
        return {
            img:[
                {img:require("@/assets/navRight02/telephone.png"),name:'充话费'},
                {img:require("@/assets/navRight02/travel.png"),name:'旅行'},
                {img:require("@/assets/navRight02/car.png"),name:'车险'},
                {img:require("@/assets/navRight02/game.png"),name:'游戏'},
                {img:require("@/assets/navRight02/lottery.png"),name:'彩票'},
                {img:require("@/assets/navRight02/cinema.png"),name:'电影'},
                {img:require("@/assets/navRight02/hotel.png"),name:'酒店'},
                {img:require("@/assets/navRight02/money.png"),name:'理财'},
            ],
            infoIndex:-1,
            ind:0,
            closeShow:false,
            test1:-1,
            show:true
        }
    },
    components:{
        NavRight02Child01,
        NavRight02Child02,
        NavRight02Child03
    },
    methods:{
        showWay:function(index){
            if (!(index >2)) {
                this.infoIndex = index;
                this.test1= this.infoIndex;
                this.show = true;
            }else {
                this.infoIndex = this.test1;
                // this.show = true;
            };
            this.ind =  this.infoIndex;
            if (!(index >3)) {
                 this.closeShow = true;
            }
        //    this.infoIndex = -1;

        },
        childData(data){
            this.infoIndex = -1;
            this.closeShow = data[0];
            this.show = false;
            // console.log(this.infoIndex);
        }
    }
}
</script>

<style lang="less" scoped>
@import "./../../themes/base.less";
.navRight02{
    width: 100%;
    ul{
        display: flex;
        flex-wrap: wrap;
        margin: 0;
        padding: 0;
        background-color: #ffffff;
    }
    li{
        box-sizing: border-box;
        border:1px solid #f4f4f4;
        list-style: none;
        height: 78px;
        width: 25%;
        text-align: center;
        padding-top:12px;
        cursor: pointer;
    }
    li:hover{
        color: @color-primary;
    }
    img{
        width: 30px;
    }
    p{
        margin:0;
        font-size: 12px;
        // width: fit-content;
    }
    .navRight02-div{
        height: 158px;
        width: 100%;
        box-sizing: border-box;
        // box-sizing: border-box;
        background-color: #ffffff;
        border: solid red 1px;
        position: relative;
        top: -79px;
        z-index: 1;
    }
    .navRight02-li{
        border: 1px solid red;
        border-bottom: 1px solid #ffffff;
        color:@color-primary;
        z-index: 3;
    }
}
</style>